@import url(https://fonts.googleapis.com/css2?family=Bai+Jamjuree&display=swap);
body {
  font-family: "Bai Jamjuree", sans-serif !important;
}
/****************************************** NAVBAR ********************************************/
.particles-canvas {
  padding-bottom: 100px;
}
nav {
  background-color: #030b16;
  z-index: 2;
}

.logo {
  width: 7.5rem;
  margin-top: 1.6rem;
  /* margin-left: 5rem; */
}

/* ------------MENU LINK--------------*/
nav a.nav-link {
  letter-spacing: 0.1rem;
  cursor: pointer;
}

nav ul li {
  text-transform: uppercase;
}

.navbar-nav > li > a {
  margin-right: 0.75rem;
  margin-left: 0.75rem;
  color: #ffffff !important;
}

.navbar-brand {
  max-width: 17.25rem;
  padding: 0 !important;
  height: 6rem !important;
}

@media (max-width: 768px) {
  .navbar-brand {
    width: 7rem;
  }
}

.navbar-nav > li > a.active,
.navbar-nav > li > a.active:focus {
  border-top: solid 0.25rem #3444f1;
  height: 2px;
  background: #3444f1;
}

.navbar-nav > li > a:hover {
  border-top: solid 0.25rem #3444f1;
  height: 2px;
  background: #3444f1;
}

/****************************************** Header ********************************************/
.header-wraper {
  position: relative;
  background-color: #030b16;
  /* background: url("../images//backgroubd.jpg") no-repeat; */
  background-size: cover;
  background-position: center;
  height: 88vh;
}

.header-wraper h1 {
  font-size: 4rem;
}

.githand {
  margin-top: -30px;
}
.main-info {
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  z-index: 1;
}

.main-info h1 {
  color: #ffffff;
  text-transform: uppercase;
}

/******************************** TYPED TEXT ********************************/
.typed-text {
  font-size: 2rem;
  color: #ffffff;
  /* background-color: #6f52ab; */
}

@media (max-width: 768px) {
  .main-info h1 {
    font-size: 1.5rem !important;
  }
}

/************************** HEADER MAIN OFFER BUTTON *************************/
.btn-main-offer {
  border: 1.5px solid #3444f1;
  text-transform: uppercase;
  padding: 0.625rem 0.75rem 0.75rem 0.75rem;
  color: #ffffff;
  margin: 2rem;
}

.btn-main-offer:hover {
  text-decoration: none;
  background-color: #ffffff;
  color: #3444f1;
  transition: 0.2s ease-in-out;
}

/************************** PARTICLES JS *************************/
.tsparticles-canvas-el {
  position: absolute;
  height: 100vh;
  width: 100vh;
  z-index: 1;
}

@media (max-width: 768px) {
  .particles-canvas {
    display: none;
  }
}

/****************************************** ABOUT ME ********************************************/
.aboutme {
  padding: 0.7rem;
  padding-top: 5rem;
  padding-bottom: 5rem;
}

.photo-wrap {
  display: flex;
  justify-content: center;
  align-items: center;
  padding-top: 70px;
}

.profile-img {
  width: 400px;
  border-radius: 50%;
  padding: 0.3rem;
  border: thick double #1900ff;
}

.about-heading {
  color: #002fff;
}

.textSE {
  color: #002fff;
}

.textSE:hover {
  color: #002fff;
  text-decoration: none;
}

.about-details1 {
  font-size: 1.5rem;
  margin-top: 5.5rem;
}

@media (max-width: 468px) {
  .profile-img {
    width: 200px;
  }
  .photo-wrap {
    margin-top: -5rem;
  }
  .about-details1 {
    margin-top: -1rem;
  }
}

/****************************************** EDUCATION ********************************************/
.education {
  background: #030b16;
  padding: 0.7rem;
  padding-top: 5rem;
  padding-bottom: 10rem;
}

.education-wrapper {
  position: relative;
  padding: 3.125rem 0;
  overflow: hidden;
}

.education h1 {
  text-align: left;
  color: #ffffff;
  padding-bottom: 1rem;
}

.piclogo {
  width: 125px;
}

.education-wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 50%;
  margin-left: -0.0625rem;
  width: 0.2rem;
  height: 100%;
  background: #ffffff;
  z-index: 1;
}

.timeline-block {
  width: calc(50% + 0.5rem);
  display: flex;
  justify-content: space-between;
}

.timeline-block-right {
  float: right;
}

.timeline-block-left {
  float: left;
  direction: rtl;
}

.marker {
  width: 1rem;
  height: 1rem;
  border-radius: 50%;
  border: 0.3125rem solid #ffffff;
  background: #ffffff;
  margin-top: 3.5rem;
  z-index: 3;
}

.timeline-content {
  width: 95%;
  padding: 0 0.9375rem;
  color: #ffffff;
  /*จุด ซ้ายขวา
  direction: rtl;
  */
  direction: ltr;
}

.timeline-content h3 {
  margin-top: 0.3125rem;
  color: #3444f1;
}

@media (max-width: 768px) {
  .education .container {
    padding-left: 0;
  }

  .education-wrapper::before {
    left: 0.5rem;
    margin-bottom: 1.875rem;
  }
  .timeline-block-right {
    float: none;
    width: 100%;
  }
  .timeline-block-left {
    float: none;
    width: 100%;
    direction: ltr;
  }
}

