/************************ SERVICES *********************************/
.skills {
  text-align: center;
  padding-bottom: 2rem;
  background: #030b16;
  padding: 0.7rem;
  padding-top: 5rem;
  padding-bottom: 15rem;
}

.skills h1 {
  color: #ffffff;
}

.skills .circle {
  position: relative;
  margin: 0.375rem auto;
  background: #3444f1;
  border-radius: 50%;
  width: 3.125rem;
  height: 3.125rem;
}

.skills .icon {
  font-size: 1.4rem;
  color: #ffffff;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.skills .box {
  height: 23rem;
  border-bottom: 0.3125rem solid #3444f1;
  padding: 0.625rem;
  margin-bottom: 1.875rem;
  background: #030b16;
  color: #ffffff;
  transition: 0.3s ease-in-out;
}

.skills .box:hover {
  background: #ffffff;
  color: #001eff;
  border-bottom: 0.3125rem solid #3444f1;
}

.skills .box:hover .icon {
  color: #ffffff;
}

.skills .box:hover .circle {
  background: #3444f1;
}

.skills .box:hover p {
  color: #001eff;
}

/****************************************** PROJECT ********************************************/

.project-wrapper {
  background: #f5f5ff;
  padding-top: 5rem;
  padding-bottom: 10rem;
