}

.project-image {
  width: 15rem;
}

.project-image-box {
  position: relative;
  margin: 1rem;
}

/*------------ OVERFLOW BOX  ------------------*/
.overflow {
  position: absolute;
  opacity: 0;
  background: #000000;
  width: 15rem;
  height: 8rem;
  top: 0;
  /* border: 2px solid #930000; */
  cursor: pointer;
}

.project-image-box:hover .overflow {
  opacity: 50%;
  height: 7.25rem;
  transition: 0.3s ease-in-out;
}

.project-icon {
  position: absolute;
  opacity: 0;
  margin-left: -8rem;
  margin-top: 3.4rem;
  justify-content: center;
  align-self: center;
  color: #00a6ff;
  font-size: 1.5rem;
}

.project-image-box:hover .project-icon {
  opacity: 85%;
  transition: 0.3s ease-in-out;
  cursor: pointer;
}

